import { Injectable, ApplicationRef, createComponent, EnvironmentInjector, ComponentRef } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { Notification } from '../components/shared/notification/notification';

/**
 * Configuraci√≥n de notificaci√≥n
 */
export interface NotificationConfig {
  type: 'success' | 'error' | 'warning' | 'info';
  title: string;
  message: string;
  duration?: number;
  position?: 'top-right' | 'top-left' | 'bottom-right' | 'bottom-left';
  autoDismiss?: boolean;
  icon?: string;
}

/**
 * NotificationService - Servicio Centralizado de Notificaciones
 *
 * PROP√ìSITO:
 * - Gestionar la creaci√≥n din√°mica de componentes de notificaci√≥n
 * - Manipular el DOM para insertar/eliminar notificaciones
 * - Proporcionar API simple para mostrar diferentes tipos de notificaciones
 * - Coordinar auto-dismiss configurable
 *
 * PATR√ìN: SEPARACI√ìN DE RESPONSABILIDADES
 *
 * COMPONENTE (Notification):
 * - ‚úÖ Renderiza la UI de la notificaci√≥n
 * - ‚úÖ Muestra animaciones
 * - ‚úÖ Emite evento cuando se cierra
 * - ‚ùå NO crea notificaciones
 * - ‚ùå NO manipula el DOM fuera de su elemento
 *
 * SERVICIO (NotificationService):
 * - ‚úÖ Crea componentes din√°micamente
 * - ‚úÖ Manipula el DOM (appendChild, removeChild)
 * - ‚úÖ Gestiona el ciclo de vida de las notificaciones
 * - ‚úÖ Proporciona API conveniente (success, error, etc.)
 * - ‚ùå NO renderiza UI
 * - ‚ùå NO maneja animaciones CSS
 *
 * FLUJO COMPLETO:
 * 1. C√≥digo llama a notificationService.success('T√≠tulo', 'Mensaje')
 * 2. Servicio crea componente Notification din√°micamente
 * 3. Servicio configura inputs del componente
 * 4. Servicio hace appendChild al body
 * 5. Componente muestra animaci√≥n de entrada
 * 6. Componente auto-dismiss despu√©s de duration
 * 7. Componente emite evento dismissed
 * 8. Servicio escucha evento y hace removeChild del DOM
 * 9. Servicio destruye el componente
 *
 * @example
 * ```typescript
 * // En cualquier componente o servicio
 * export class MyComponent {
 *   private notificationService = inject(NotificationService);
 *
 *   onSave() {
 *     this.notificationService.success('Guardado', 'Los cambios se guardaron correctamente');
 *   }
 *
 *   onError() {
 *     this.notificationService.error('Error', 'No se pudo guardar', 10000);
 *   }
 * }
 * ```
 */
@Injectable({
  providedIn: 'root',
})
export class NotificationService {
  /**
   * Lista de notificaciones activas en el DOM
   * Almacena ComponentRef para poder destruirlas despu√©s
   */
  private notifications: ComponentRef<Notification>[] = [];

  /**
   * Subject para emitir eventos de notificaciones
   * Permite a otros servicios/componentes escuchar cuando se muestran notificaciones
   */
  private notificationSubject = new Subject<NotificationConfig>();

  /**
   * Observable p√∫blico para subscripciones externas
   */
  public notification$: Observable<NotificationConfig> = this.notificationSubject.asObservable();

  constructor(
    private appRef: ApplicationRef,
    private injector: EnvironmentInjector
  ) {}

  /**
   * M√âTODO PRINCIPAL: Mostrar notificaci√≥n
   *
   * WORKFLOW:
   * 1. Crear componente din√°micamente con createComponent()
   * 2. Configurar inputs del componente (type, title, message, etc.)
   * 3. Subscribirse al evento dismissed del componente
   * 4. A√±adir al √°rbol de Angular (attachView)
   * 5. Manipular DOM: appendChild al body
   * 6. Emitir evento en el Subject para observadores
   * 7. Guardar referencia para limpieza posterior
   *
   * @param config - Configuraci√≥n de la notificaci√≥n
   *
   * @example
   * ```typescript
   * notificationService.show({
   *   type: 'success',
   *   title: 'Guardado',
   *   message: 'Los cambios se guardaron correctamente',
   *   duration: 5000,
   *   position: 'top-right'
   * });
   * ```
   */
  show(config: NotificationConfig): void {
    // 1. Crear el componente din√°micamente
    const componentRef = createComponent(Notification, {
      environmentInjector: this.injector,
    });

    // 2. Configurar los inputs del componente
    componentRef.setInput('type', config.type);
    componentRef.setInput('title', config.title);
    componentRef.setInput('message', config.message);
    componentRef.setInput('position', config.position || 'top-right');
    componentRef.setInput('duration', config.duration || 5000);
    componentRef.setInput('autoDismiss', config.autoDismiss !== false);

    if (config.icon) {
      componentRef.setInput('icon', config.icon);
    }

    // 3. Suscribirse al evento de cierre
    componentRef.instance.dismissed.subscribe(() => {
      this.remove(componentRef);
    });

    // 4. A√±adir al √°rbol de Angular para detecci√≥n de cambios
    this.appRef.attachView(componentRef.hostView);

    // 5. MANIPULACI√ìN DIRECTA DEL DOM: Obtener el elemento HTML y a√±adirlo al body
    const domElem = (componentRef.hostView as any).rootNodes[0] as HTMLElement;

    // Configurar la duraci√≥n como CSS custom property para la animaci√≥n
    domElem.style.setProperty('--notification-duration', `${config.duration || 5000}ms`);

    // IMPORTANTE: Asegurar posici√≥n inicial para que no se vea antes de calcular
    domElem.style.position = 'fixed';
    if (config.position?.includes('right')) {
      domElem.style.right = '2rem';
    } else {
      domElem.style.left = '2rem';
    }
    // Oculto hasta que se calcule la posici√≥n
    domElem.style.visibility = 'hidden';

    document.body.appendChild(domElem);

    // 6. Emitir evento para observadores externos
    this.notificationSubject.next(config);

    // 7. Guardar referencia para poder eliminarlo despu√©s
    this.notifications.push(componentRef);

    // 8. CALCULAR POSICI√ìN Y MOSTRAR
    // Usamos doble requestAnimationFrame para asegurar que el DOM est√° pintado
    requestAnimationFrame(() => {
      // Forzar detecci√≥n de cambios para asegurar que el componente proces√≥ el ngOnInit
      componentRef.changeDetectorRef.detectChanges();

      // Segundo frame para asegurar que las alturas est√°n calculadas
      requestAnimationFrame(() => {
        // Calcular posiciones de todas las notificaciones
        this.updatePositions();

        // Hacer visible con una peque√±a transici√≥n
        domElem.style.visibility = 'visible';
      });
    });
  }

  /**
   * M√âTODO PRIVADO: Eliminar notificaci√≥n del DOM
   *
   * WORKFLOW:
   * 1. Obtener elemento HTML del ComponentRef
   * 2. Manipular DOM: removeChild del parent
   * 3. Desconectar de Angular (detachView)
   * 4. Destruir componente (destroy)
   * 5. Remover de la lista de notificaciones activas
   *
   * @param componentRef - Referencia al componente a eliminar
   */
  private remove(componentRef: ComponentRef<Notification>): void {
    // 1. Obtener el elemento del DOM
    const domElem = (componentRef.hostView as any).rootNodes[0] as HTMLElement;

    // 2. MANIPULACI√ìN DIRECTA DEL DOM: Remover del body
    if (domElem && domElem.parentNode) {
      domElem.parentNode.removeChild(domElem);
    }

    // 3. Limpiar de Angular (detach del √°rbol de change detection)
    this.appRef.detachView(componentRef.hostView);

    // 4. Destruir el componente liberando recursos
    componentRef.destroy();

    // 5. Remover de la lista de notificaciones activas
    const index = this.notifications.indexOf(componentRef);
    if (index !== -1) {
      this.notifications.splice(index, 1);
    }

    // 6. Actualizar posiciones del resto de notificaciones inmediatamente
    // Esto hace que las notificaciones restantes se desplacen hacia arriba/abajo
    if (this.notifications.length > 0) {
      this.updatePositions();
    }
  }

  /**
   * M√âTODO PRIVADO: Actualizar posiciones de todas las notificaciones
   *
   * L√ìGICA DE APILAMIENTO:
   * - Top positions (top-right, top-left):
   *   * M√°s antigua arriba (√≠ndice 0)
   *   * M√°s nueva abajo (√∫ltimo √≠ndice)
   *   * Cada notificaci√≥n se desplaza hacia abajo sumando alturas
   * - Bottom positions (bottom-right, bottom-left):
   *   * M√°s antigua abajo (√≠ndice 0)
   *   * M√°s nueva arriba (√∫ltimo √≠ndice)
   *   * Cada notificaci√≥n se desplaza hacia arriba sumando alturas
   *
   * WORKFLOW:
   * 1. Agrupar notificaciones por posici√≥n
   * 2. Para cada grupo, calcular offset acumulativo
   * 3. Aplicar offset como inline style al DOM
   */
  private updatePositions(): void {
    // Agrupar notificaciones por posici√≥n
    const grouped: { [key: string]: ComponentRef<Notification>[] } = {};

    this.notifications.forEach(ref => {
      const position = ref.instance.position || 'top-right';
      if (!grouped[position]) grouped[position] = [];
      grouped[position].push(ref);
    });

    // Constantes de posicionamiento
    const INITIAL_OFFSET = 20; // Margen inicial desde el borde
    const GAP = 12; // Espacio entre notificaciones
    const DEFAULT_HEIGHT = 80; // Altura por defecto si no se puede leer

    Object.keys(grouped).forEach(posKey => {
      const group = grouped[posKey];
      let currentOffset = INITIAL_OFFSET;

      group.forEach((ref, index) => {
        const hostElem = (ref.hostView as any).rootNodes[0] as HTMLElement;

        if (!hostElem) {
          return;
        }

        // Obtener el elemento .notification interno (el que tiene position: fixed)
        const domElem = hostElem.querySelector('.notification') as HTMLElement;

        if (!domElem) {
          return;
        }

        // Forzar lectura de altura - getBoundingClientRect es m√°s preciso
        const rect = domElem.getBoundingClientRect();
        const elementHeight = rect.height > 0 ? rect.height : DEFAULT_HEIGHT;

        // APLICAR POSICI√ìN con transici√≥n suave
        domElem.style.transition = 'top 0.3s ease, bottom 0.3s ease';

        if (posKey.startsWith('top')) {
          // Desde arriba hacia abajo
          domElem.style.top = `${currentOffset}px`;
          domElem.style.bottom = 'auto';
        } else {
          // Desde abajo hacia arriba
          domElem.style.bottom = `${currentOffset}px`;
          domElem.style.top = 'auto';
        }

        // Acumular offset para la siguiente notificaci√≥n
        currentOffset += elementHeight + GAP;
      });
    });
  }

  /**
   * Limpiar todas las notificaciones activas
   * √ötil al navegar entre p√°ginas o al cerrar sesi√≥n
   *
   * @example
   * ```typescript
   * // Al cerrar sesi√≥n
   * notificationService.clearAll();
   * ```
   */
  clearAll(): void {
    this.notifications.forEach(ref => {
      const domElem = (ref.hostView as any).rootNodes[0] as HTMLElement;
      if (domElem && domElem.parentNode) {
        domElem.parentNode.removeChild(domElem);
      }
      this.appRef.detachView(ref.hostView);
      ref.destroy();
    });
    this.notifications = [];
  }

  // ==========================================================================
  // M√âTODOS DE CONVENIENCIA
  // Proporcionan API simple para los casos m√°s comunes
  // ==========================================================================

  /**
   * Mostrar notificaci√≥n de √âXITO
   *
   * @param title - T√≠tulo de la notificaci√≥n
   * @param message - Mensaje descriptivo
   * @param duration - Duraci√≥n en ms (default: 5000)
   *
   * @example
   * ```typescript
   * notificationService.success('Guardado', 'Los cambios se guardaron correctamente');
   * ```
   */
  success(title: string, message: string, duration: number = 5000): void {
    this.show({
      type: 'success',
      title,
      message,
      duration,
      position: 'top-right',
    });
  }

  /**
   * Mostrar notificaci√≥n de ERROR
   * Por defecto dura m√°s tiempo (8000ms) para asegurar que el usuario la vea
   *
   * @param title - T√≠tulo del error
   * @param message - Mensaje descriptivo del error
   * @param duration - Duraci√≥n en ms (default: 8000)
   *
   * @example
   * ```typescript
   * notificationService.error('Error', 'No se pudo guardar el √°lbum', 10000);
   * ```
   */
  error(title: string, message: string, duration: number = 8000): void {
    this.show({
      type: 'error',
      title,
      message,
      duration,
      position: 'top-right',
    });
  }

  /**
   * Mostrar notificaci√≥n de ADVERTENCIA
   *
   * @param title - T√≠tulo de la advertencia
   * @param message - Mensaje descriptivo
   * @param duration - Duraci√≥n en ms (default: 6000)
   *
   * @example
   * ```typescript
   * notificationService.warning('Atenci√≥n', 'Este √°lbum ya est√° en favoritos');
   * ```
   */
  warning(title: string, message: string, duration: number = 6000): void {
    this.show({
      type: 'warning',
      title,
      message,
      duration,
      position: 'top-right',
    });
  }

  /**
   * Mostrar notificaci√≥n INFORMATIVA
   *
   * @param title - T√≠tulo de la informaci√≥n
   * @param message - Mensaje descriptivo
   * @param duration - Duraci√≥n en ms (default: 5000)
   *
   * @example
   * ```typescript
   * notificationService.info('Actualizaci√≥n', 'Hay 3 √°lbumes nuevos disponibles');
   * ```
   */
  info(title: string, message: string, duration: number = 5000): void {
    this.show({
      type: 'info',
      title,
      message,
      duration,
      position: 'top-right',
    });
  }

  /**
   * Mostrar notificaci√≥n PERSISTENTE (sin auto-dismiss)
   * El usuario debe cerrarla manualmente
   *
   * @param type - Tipo de notificaci√≥n
   * @param title - T√≠tulo
   * @param message - Mensaje
   *
   * @example
   * ```typescript
   * // Para errores cr√≠ticos que requieren acci√≥n del usuario
   * notificationService.persistent('error', 'Conexi√≥n perdida', 'No se puede conectar al servidor');
   * ```
   */
  persistent(
    type: 'success' | 'error' | 'warning' | 'info',
    title: string,
    message: string
  ): void {
    this.show({
      type,
      title,
      message,
      autoDismiss: false, // No se cierra autom√°ticamente
    });
  }

  /**
   * Obtener n√∫mero de notificaciones activas
   * √ötil para debugging o mostrar contador
   *
   * @returns Cantidad de notificaciones en pantalla
   */
  getActiveCount(): number {
    return this.notifications.length;
  }
}
