import { Component, Input, Output, EventEmitter, OnInit, OnDestroy, signal, ElementRef, AfterViewInit } from '@angular/core';
import { CommonModule } from '@angular/common';

/**
 * Notification Component - Componente de Notificaci√≥n/Toast
 *
 * RESPONSABILIDADES (Solo Presentaci√≥n):
 * - ‚úÖ Renderizar la UI de la notificaci√≥n
 * - ‚úÖ Mostrar animaciones de entrada/salida
 * - ‚úÖ Emitir evento cuando se cierra (dismissed)
 * - ‚úÖ Gestionar auto-dismiss con timer
 * - ‚úÖ Aplicar clases CSS seg√∫n type y position
 * - ‚ùå NO crear notificaciones (lo hace el servicio)
 * - ‚ùå NO manipular DOM fuera de su elemento
 * - ‚ùå NO contener l√≥gica de negocio
 *
 * PATR√ìN: COMPONENTE DE PRESENTACI√ìN (Presentational Component)
 *
 * Este componente es "tonto" (dumb component):
 * - Recibe datos por @Input
 * - Emite eventos por @Output
 * - No tiene l√≥gica de negocio
 * - No interact√∫a con servicios (excepto ser creado por uno)
 *
 * FLUJO DE VIDA:
 * 1. NotificationService crea el componente din√°micamente
 * 2. NotificationService configura los @Input (type, title, message, etc.)
 * 3. ngOnInit() se ejecuta:
 *    - Inicia animaci√≥n de entrada (isVisible = true)
 *    - Configura timer de auto-dismiss si est√° activado
 * 4. Usuario puede cerrar manualmente haciendo click en ‚úï
 * 5. Timer expira o usuario cierra manualmente
 * 6. onDismiss() ejecuta:
 *    - Inicia animaci√≥n de salida (isVisible = false)
 *    - Emite evento dismissed despu√©s de animaci√≥n
 * 7. NotificationService escucha dismissed y elimina del DOM
 *
 * @example
 * ```typescript
 * // El servicio lo usa as√≠:
 * const componentRef = createComponent(Notification, {...});
 * componentRef.setInput('type', 'success');
 * componentRef.setInput('title', 'Guardado');
 * componentRef.setInput('message', 'Los cambios se guardaron');
 * componentRef.setInput('duration', 5000);
 * componentRef.instance.dismissed.subscribe(() => {
 *   // Eliminar del DOM
 * });
 * ```
 */
@Component({
  selector: 'app-notification',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './notification.html',
  styleUrl: './notification.scss'
})
export class Notification implements OnInit, OnDestroy, AfterViewInit {
  /**
   * INPUTS: Configuraci√≥n de la notificaci√≥n
   * Estos valores son inyectados por NotificationService con setInput()
   */

  /** Tipo de notificaci√≥n: determina color e icono */
  @Input() type: 'success' | 'error' | 'warning' | 'info' = 'info';

  /** T√≠tulo principal de la notificaci√≥n */
  @Input() title: string = '';

  /** Mensaje descriptivo */
  @Input() message: string = '';

  /** Icono personalizado (opcional, si no se proporciona usa defaultIcon) */
  @Input() icon: string = '';

  /** Posici√≥n en la pantalla */
  @Input() position: 'top-right' | 'top-left' | 'bottom-right' | 'bottom-left' = 'top-right';

  /** Si debe cerrarse autom√°ticamente */
  @Input() autoDismiss: boolean = true;

  /** Duraci√≥n en ms antes de auto-dismiss */
  @Input() duration: number = 5000;

  /**
   * √çndice de apilado para notificaciones est√°ticas (usadas en templates)
   * El servicio din√°mico NO usa esto - calcula posiciones √©l mismo
   * -1 significa que no se usa apilado est√°tico
   */
  @Input() stackIndex: number = -1;

  /**
   * Callback para obtener la altura real de esta notificaci√≥n
   * Usado por el sistema de apilado para c√°lculos din√°micos
   */
  @Input() getHeightAt?: (index: number) => number;

  /**
   * OUTPUT: Evento emitido cuando se cierra la notificaci√≥n
   * NotificationService se subscribe a este evento para eliminar del DOM
   */
  @Output() dismissed = new EventEmitter<void>();

  /**
   * ESTADO INTERNO: Control de visibilidad para animaciones
   * Signal moderno de Angular para reactividad
   */
  isVisible = signal(false);

  /**
   * ESTADO INTERNO: Control de pausa del timer
   * True cuando el mouse est√° sobre la notificaci√≥n
   */
  isPaused = signal(false);

  /**
   * Altura real del elemento (medida despu√©s del render)
   */
  private measuredHeight: number = 0;

  /**
   * TIMER: ID del timeout para auto-dismiss
   * Lo guardamos para poder cancelarlo si se cierra manualmente
   */
  private timeoutId?: number;

  /**
   * TIMER: Tiempo restante en ms
   * Se usa para reanudar el timer despu√©s de pausar
   */
  private remainingTime: number = 0;

  /**
   * TIMER: Timestamp cuando se pausa
   * Se usa para calcular el tiempo transcurrido
   */
  private pausedAt: number = 0;

  constructor(private elementRef: ElementRef) {}

  /**
   * LIFECYCLE: Despu√©s de que la vista se renderice
   * Medimos la altura real del elemento
   */
  ngAfterViewInit(): void {
    // Medir altura real despu√©s del render
    const notificationEl = this.elementRef.nativeElement.querySelector('.notification');
    if (notificationEl) {
      this.measuredHeight = notificationEl.getBoundingClientRect().height;
    }
  }

  /**
   * Obtener la altura medida del elemento
   */
  getHeight(): number {
    return this.measuredHeight || 92; // Fallback a altura estimada
  }

  /**
   * LIFECYCLE: Inicializaci√≥n del componente
   *
   * WORKFLOW:
   * 1. Espera 10ms y activa animaci√≥n de entrada (isVisible = true)
   * 2. Si autoDismiss est√° activado, configura timer
   * 3. Cuando expira el timer, llama a onDismiss()
   */
  ngOnInit(): void {
    // Trigger animaci√≥n de entrada despu√©s de un breve delay
    // Esto permite que el componente se renderice antes de animar
    setTimeout(() => {
      this.isVisible.set(true);
    }, 10);

    // Inicializar tiempo restante
    this.remainingTime = this.duration;

    // Configurar auto-dismiss si est√° activado
    if (this.autoDismiss && this.duration > 0) {
      this.startTimer();
    }
  }

  /**
   * LIFECYCLE: Limpieza del componente
   * Cancela el timer si existe para evitar memory leaks
   */
  ngOnDestroy(): void {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
    }
  }

  /**
   * HANDLER: Cerrar notificaci√≥n
   *
   * WORKFLOW:
   * 1. Desactiva visibilidad para animaci√≥n de salida
   * 2. Espera 300ms (duraci√≥n de la animaci√≥n CSS)
   * 3. Emite evento dismissed
   * 4. Cancela timer si existe
   * 5. NotificationService recibe el evento y elimina del DOM
   *
   * Puede ser llamado por:
   * - Click del usuario en el bot√≥n ‚úï
   * - Timer de auto-dismiss
   */
  onDismiss(): void {
    // Iniciar animaci√≥n de salida
    this.isVisible.set(false);

    // Esperar a que termine la animaci√≥n antes de emitir
    // IMPORTANTE: Este delay debe coincidir con la duraci√≥n en notification.scss
    setTimeout(() => {
      this.dismissed.emit();

      // Limpiar timer si existe
      if (this.timeoutId) {
        clearTimeout(this.timeoutId);
      }
    }, 300); // Duraci√≥n de la animaci√≥n CSS (transition: all 0.3s)
  }

  /**
   * COMPUTED: Clases CSS din√°micas
   * Genera string de clases seg√∫n el estado del componente
   *
   * @returns String con todas las clases CSS
   *
   * @example
   * "notification notification--success notification--top-right notification--visible"
   */
  get notificationClasses(): string {
    let classes = 'notification';
    classes += ` notification--${this.type}`;
    classes += ` notification--${this.position}`;
    if (this.isVisible()) {
      classes += ' notification--visible';
    }
    return classes;
  }

  /**
   * COMPUTED: Estilos inline para posicionamiento de apilado est√°tico
   * Solo se aplica si stackIndex >= 0 (notificaciones en template)
   * Las notificaciones din√°micas (servicio) no usan esto
   *
   * @returns Objeto de estilos para [ngStyle]
   */
  get stackStyles(): { [key: string]: string } {
    // Si no hay stackIndex definido, no aplicar estilos de apilado
    if (this.stackIndex < 0) {
      return {};
    }

    const INITIAL_OFFSET = 20; // Margen inicial desde el borde
    const GAP = 12; // Espacio entre notificaciones

    // Calcular offset sumando alturas de notificaciones anteriores
    let offset = INITIAL_OFFSET;

    if (this.getHeightAt) {
      // Usar callback para obtener alturas din√°micas
      for (let i = 0; i < this.stackIndex; i++) {
        offset += this.getHeightAt(i) + GAP;
      }
    } else {
      // Fallback: usar altura estimada
      const ESTIMATED_HEIGHT = 92;
      offset = INITIAL_OFFSET + (this.stackIndex * (ESTIMATED_HEIGHT + GAP));
    }

    // Aplicar al eje vertical seg√∫n la posici√≥n
    if (this.position.startsWith('top')) {
      return { 'top': `${offset}px` };
    } else {
      return { 'bottom': `${offset}px` };
    }
  }

  /**
   * COMPUTED: Icono por defecto seg√∫n tipo
   * Si no se proporciona icono personalizado, usa estos
   *
   * @returns Emoji/s√≠mbolo para mostrar
   */
  get defaultIcon(): string {
    // Si hay icono personalizado, usarlo
    if (this.icon) {
      return this.icon;
    }

    // Iconos por defecto seg√∫n el tipo
    switch (this.type) {
      case 'success':
        return '‚úì'; // Check mark
      case 'error':
        return '‚úï'; // X mark
      case 'warning':
        return '‚ö†'; // Warning triangle
      case 'info':
        return '‚Ñπ'; // Info symbol
      default:
        return '‚Ñπ';
    }
  }

  /**
   * M√âTODO PRIVADO: Iniciar timer de auto-dismiss
   */
  private startTimer(): void {
    this.timeoutId = window.setTimeout(() => {
      this.onDismiss();
    }, this.remainingTime);
  }

  /**
   * M√âTODO PRIVADO: Pausar timer
   * Guarda el tiempo restante para poder reanudar despu√©s
   */
  private pauseTimer(): void {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
      this.timeoutId = undefined;
      this.pausedAt = Date.now();
    }
  }

  /**
   * M√âTODO PRIVADO: Reanudar timer
   * Calcula el tiempo que pas√≥ desde la pausa y ajusta remainingTime
   */
  private resumeTimer(): void {
    if (this.pausedAt > 0) {
      const elapsed = Date.now() - this.pausedAt;
      this.remainingTime = Math.max(0, this.remainingTime - elapsed);
      this.pausedAt = 0;

      if (this.remainingTime > 0) {
        this.startTimer();
      }
    }
  }

  /**
   * HANDLER: Mouse entra en la notificaci√≥n
   * Pausa el timer de auto-dismiss
   */
  onMouseEnter(): void {
    if (this.autoDismiss) {
      this.isPaused.set(true);
      this.pauseTimer();
    }
  }

  /**
   * HANDLER: Mouse sale de la notificaci√≥n
   * Reanuda el timer de auto-dismiss
   */
  onMouseLeave(): void {
    if (this.autoDismiss) {
      this.isPaused.set(false);
      this.resumeTimer();
    }
  }
}
