import { Component, Input, ViewChild, ElementRef, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-carousel',
  standalone: true,
  imports: [],
  templateUrl: './carousel.html',
  styleUrl: './carousel.scss'
})
export class Carousel implements AfterViewInit {
  @Input() title: string = '';
  @Input() itemsToShow: number = 4; // Cu√°ntas cards mostrar a la vez
  @ViewChild('carouselTrack') carouselTrack!: ElementRef<HTMLDivElement>;

  currentIndex: number = 0;
  canScrollLeft: boolean = false;
  canScrollRight: boolean = true;

  ngAfterViewInit(): void {
    // Detectar si hay scroll disponible despu√©s de que el contenido se renderice
    setTimeout(() => {
      this.updateScrollButtons();
    }, 300);

    // Tambi√©n actualizar cuando cambie el tama√±o de la ventana
    if (typeof window !== 'undefined') {
      window.addEventListener('resize', () => this.updateScrollButtons());
    }
  }

  scrollLeft(): void {
    const track = this.carouselTrack.nativeElement;
    const scrollAmount = track.offsetWidth / this.itemsToShow;

    track.scrollBy({
      left: -scrollAmount,
      behavior: 'smooth'
    });

    setTimeout(() => {
      this.updateScrollButtons();
    }, 300);
  }

  scrollRight(): void {
    const track = this.carouselTrack.nativeElement;
    const scrollAmount = track.offsetWidth / this.itemsToShow;

    track.scrollBy({
      left: scrollAmount,
      behavior: 'smooth'
    });

    setTimeout(() => {
      this.updateScrollButtons();
    }, 300);
  }

  private updateScrollButtons(): void {
    if (!this.carouselTrack) return;

    const track = this.carouselTrack.nativeElement;

    // Puede hacer scroll a la izquierda si no est√° al principio
    this.canScrollLeft = track.scrollLeft > 10;

    // Puede hacer scroll a la derecha si no est√° al final
    const maxScroll = track.scrollWidth - track.clientWidth;
    this.canScrollRight = track.scrollLeft < maxScroll - 10;
  }

  /**
   * MANIPULACI√ìN DOM AVANZADA: Modificar estilos din√°micamente
   * A√±ade un efecto de highlight al track del carousel
   */
  toggleHighlight(): void {
    if (!this.carouselTrack) return;

    const track = this.carouselTrack.nativeElement;

    // MANIPULACI√ìN DIRECTA DEL DOM: Modificar estilos con nativeElement.style
    if (track.style.boxShadow === '') {
      // Aplicar estilos din√°micamente
      track.style.boxShadow = '0 0 20px rgba(255, 215, 0, 0.6)';
      track.style.border = '2px solid gold';
      track.style.transition = 'all 0.3s ease';
    } else {
      // Remover estilos
      track.style.boxShadow = '';
      track.style.border = '';
    }
  }

  /**
   * MANIPULACI√ìN DOM: Cambiar opacidad din√°micamente
   */
  setOpacity(value: number): void {
    if (!this.carouselTrack) return;

    // MANIPULACI√ìN DIRECTA: modificar estilo de opacidad
    this.carouselTrack.nativeElement.style.opacity = value.toString();
    this.carouselTrack.nativeElement.style.transition = 'opacity 0.3s ease';
  }
}
